  import uniRate from '../../components/uni-rate.vue'
  import urls from '../../../utils/urls.js'
  export default {
    components: {uniRate},
    data () {
      return {
        star: 4,
        current: 0,
				isLogin: this.$store.state.isLogin
      }
    },
    methods: {
      starChange: function (v) {
        this.star = v.value
      },
      getContent: function (event) {
        var value = event.detail.value
        this.current = value.length
      },
      sure: function (e) {
				if(this.$store.state.isLogin==false){
					uni.showModal({
					  title: '提示',
					  content: '您还没有登录，是否登录？',
					  success: (res) => {
					    if (res.confirm) {
					      uni.navigateTo({
					        url: '../login'
					      })
					    }
					  }
					})
				}else{
					let id = '';
					if(this.isLogin){
						id = JSON.parse(uni.getStorageSync('userInfo')).id
					}else{
						 id = 0
					}
					
					// var id = JSON.parse(uni.getStorageSync("userInfo")).id
					  var bookId = this.$store.state.bookId
					  var content = e.target.value.content
					  if (content) {
					    uni.request({
					      url: urls.HTTP + '/comment/addcomment',
					      method: 'POST',
					      data: {
					        uid: id,
					        novel_id: bookId,
					        content: content
					      },
					      success: (res) => {
					        // console.log(res)
					        if (res.data.code === 0) {
					          uni.showModal({
					            title: '提示',
					            content: res.data.error,
					            showCancel: false,
					            success: (res) => {
					              uni.redirectTo({
					                url: 'storyDetail'
					              })
					            }
					          })
					        } else {
					          uni.showToast({
					            title: res.data.error,
					            icon: 'none'
					          })
					        }
					      }
					    })
					  }
					}
				}
        // console.log(e)
    },
    computed: {
      rate () {
        return Math.floor(this.star * 2)
      }
    }
  }
